.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #5704af;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main_image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.main_image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

@media only screen and (max-width: 768px) {
  .header h1 {
    font-size: 1.5rem;
    margin: 5px
  }

  .header {
    padding: 0 3%;
  }
}
